@import "./variables";

@mixin textDescription() {
  text-align: center;
  font: $textFontDescription;
  letter-spacing: 0.03em;
  color: $textColor;
  background-color: $primaryColor;
  -webkit-font-smoothing: antialiased;
  border-radius: 0 0 15px 15px;
  padding: 2%;
}

@mixin generalBlock() {
  background-color: $backgroundColorFirst;
  align-items: center;
  height: 100vh;
}

@mixin container() {
  width: 80%;
  height: 100%;
  //border: 1px rgb(0, 0, 0) solid;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 0 0 8vh 0;
}

@mixin imageCenterProperty() {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}



@mixin form() {
  .divForm {
    position: relative;
    padding: 15px 0 0;
    min-width: 400px;
    height: auto;
  }

  input {
    font-family: inherit;
    width: 100%;
    border: 0;
    border-bottom: 2px solid $gray;
    outline: 0;
    font-size: 1.3rem;
    color: $white;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;

    &::placeholder {
      color: transparent;
    }

    &:placeholder-shown ~ .inputLabel {
      font-size: 1.3rem;
      cursor: text;
      top: 20px;
    }
  }

  .inputLabel {
    position: absolute;
    top: 0;
    display: block;
    transition: all linear .5s;
    font-size: 1rem;
    color: $gray;
  }

  input:focus {
    ~ .inputLabel {
      position: absolute;
      top: 0;
      display: block;
      transition: 0.2s;
      font-size: 1rem;
      color: $primary;
      font-weight: 700;
    }

    padding-bottom: 6px;
    font-weight: 700;
    border-width: 3px;
    border-image: linear-gradient(to right, $primary, $secondary);
    border-image-slice: 1;
  }

  button:hover {
    background: $white;
    color: $black;
  }
}